import React from 'react'
import PropTypes from 'prop-types'
import styled from 'styled-components'

import Link from 'components/Link'
import Section from '../IntersectedSection'

/*
TO CHECK
*/

const MoreInfo = ({ className, ...props }) => (
  <Section
    title="More stuff"
    bgColor="#f3efe0"
    hdColor="#22a39f"
    className={className}
    {...props}
  >
    <div className="columns">
      <div className="column is-6">
        <Link href="/projects.html">
          <h3>Projects</h3>
          <p>More info</p>
        </Link>
      </div>
      <div className="column is-6">
        <Link href="/publications.html">
          <h3>Publications</h3>
          <p>More info</p>
        </Link>
      </div>
    </div>
    <div className="columns">
      <div className="column is-6">
        <Link href="/research_projects.html">
          <h3>R&amp;D Projects</h3>
          <p>More info</p>
        </Link>
      </div>
      <div className="column is-6">
        <Link href="/teaching.html">
          <h3>Teaching</h3>
          <p>More info</p>
        </Link>
      </div>
    </div>
  </Section>
)

MoreInfo.propTypes = {
  className: PropTypes.string
}

export default styled(MoreInfo)`
  h3 {
    color: #000;
    font-weight: bold;
  }

  a {
    width: 100%;
  }

  a:hover {
    background-color: #fafafa;
  }
`
